import { Controller } from '@ali/4ever-cangjie';
;
import { convertKeysToContainer } from './convertKeysToContainer';

export const transform = (controller: Controller): Controller => {
  const blocks = controller.query('getTransformNodes');
  if (Array.isArray(blocks)) {
    controller.command(
      convertKeysToContainer,
      blocks.map((b) => b.key),
    );
  }
  return controller;
};
